table.r-responsive-table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: fixed;
}

table.r-responsive-table tr {
  border: 1px solid #ddd;
  padding: 0.35em;
  word-wrap: break-word;
}
table.r-responsive-table th,
table.r-responsive-table td {
  padding: 0.625em;
  text-align: center;
}
table.r-responsive-table th {
  font-size: 0.85em;
}
table.r-responsive-table .tdBefore {
  display: none;
}
